*{
    color: white;
}
body{
    min-width: 100%;
    min-height: 100vh;
    &::before{
        content: "";
        position: fixed;
        width: 100vw;
        height: 100vh;
        z-index: -1;
        top: 0;
        left: 0;
        background-image: url("/public/background.png");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;

    }
}

:root{
    --small-border: 1px;
}

/*
    Used to prevent unwanted start transition of elements at page load
    Must be removed by JS on page load
*/
.preload * {
    -webkit-transition: none !important;
    -moz-transition: none !important;
    -ms-transition: none !important;
    -o-transition: none !important;
    transition: none !important;
    opacity: 0 !important;
}

@media (min-width: 2000px) {
    :root{
        --small-border: 2px;
        font-size: 20px;
    }
}

@media (min-width: 3000px) {
    :root{
        --small-border: 3px;
        font-size: 30px;
    }
}